     .login-container {
         width: 300px;
         margin: 100px auto;
         padding: 20px;
         border: 1px solid #ccc;
         border-radius: 5px;
         box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
     }

     .login-container h2 {
         text-align: center;
         margin-bottom: 20px;
     }

     .form-group {
         margin-bottom: 15px;
     }

     .form-group label {
         display: block;
         margin-bottom: 5px;
     }

     .form-group input {
         width: 100%;
         padding: 8px;
         border: 1px solid #ccc;
         border-radius: 3px;
     }

     .login-container button {
         width: 100%;
         padding: 10px;
         background-color: #007bff;
         color: white;
         border: none;
         border-radius: 3px;
         cursor: pointer;
     }

     .login-container button:hover {
         background-color: #0056b3;
     }